<template>
    <div>
        <top-nav title="商城设置"></top-nav>
        <div class="wyu-main" v-loading="loading">
            <div class="wyu-block">
                <div class="wyu-tips wyu-tips-info" style="margin-bottom:10px;">
                    <p class="tit"> 1）这里是商城设置</p>
                    <p class="tit"> 2）分销需要启用，关闭分销后分销设置将不起作用</p>
                    <p class="tit"> 3）启用快递鸟需要注册快递鸟用户 <a target="_blank" href="https://www.kdniao.com/reg">申请快递鸟用户传送门</a></p>
                    <p class="tit"> 4）剩余库存数大于设定的库存充足将会在库存管理中显示库存充足</p>
                    <p class="tit"> 5）剩余库存数小于设定的库存充足并且大于设定的库存紧张数将会在库存管理中显示需要关注</p>
                </div>

                <el-form ref="form" :model="form" label-width="150px" size="mini">
                    <div class="wyu-title">
                        <h2>是否开启分销</h2>
                    </div>
                    <el-form-item label="是否开启分销" >
                        <el-switch v-model="form.is_fenxiao" :value="form.is_fenxiao" :active-value=1 :inactive-value=0></el-switch>
                    </el-form-item>

                    <div class="wyu-title">
                        <h2>提现限额设置</h2>
                    </div>
                    <el-form-item label="用户最低提现金额" >
                        <el-input v-model="form.user_withdraw_limit" style="width: 500px" placeholder=""></el-input>
                    </el-form-item>

                    <el-form-item label="推广员最低提现金额" >
                        <el-input v-model="form.agent_withdraw_limit" style="width: 500px" placeholder=""></el-input>
                    </el-form-item>

                    <div class="wyu-title">
                        <h2>VIP设置</h2>
                    </div>
                    <el-form-item label="PLUS会员价格" >
                        <el-input v-model="form.vip_price" style="width: 500px" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="PLUS会员划线价格" >
                        <el-input v-model="form.vip_huaxian_price" style="width: 500px" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="VIP会员时间(天)" >
                        <el-input v-model="form.vip_expire_time" style="width: 500px" placeholder=""></el-input>
                    </el-form-item>

                    <div class="wyu-title">
                        <h2>快递鸟设置</h2>
                    </div>
                    <el-form-item label="是否显示">
                        <el-switch v-model="form.is_open" :active-value="1" :inactive-value="0"></el-switch>
                    </el-form-item>
                    <el-form-item label="usinessid" >
                        <el-input v-model="form.usinessid" style="width: 500px" placeholder="请输入快递鸟usinessid"></el-input>
                    </el-form-item>
                    <el-form-item label="appkey" >
                        <el-input v-model="form.appkey" style="width: 500px" placeholder="请输入快递鸟appkey"></el-input>
                    </el-form-item>

                    <div class="wyu-title">
                        <h2>库存预警</h2>
                    </div>
                    <el-form-item label="无货" >
                        <el-input v-model="form.sold_out" style="width: 500px" placeholder="请输入数量"></el-input>
                    </el-form-item>

                    <el-form-item label="即将售罄" >
                        <el-input v-model="form.critical" style="width: 500px" placeholder="请输入数量"></el-input>
                    </el-form-item>

                    <el-form-item label="库存紧张" >
                        <el-input v-model="form.danger" style="width: 500px" placeholder="请输入数量"></el-input>
                    </el-form-item>

                    <el-form-item label="库存充足" >
                        <el-input v-model="form.safe" style="width: 500px" placeholder="请输入数量"></el-input>
                    </el-form-item>


                    <div class="wyu-title">
                        <h2>百度SEO设置</h2>
                    </div>
                    <el-form-item label="百度SEO标题" label-width="120px">
                        <el-input v-model="form.seo.title" style="width:500px;"  autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="百度SEO描述" label-width="120px">
                        <el-input type="textarea" :autosize="{minRows: 2, maxRows: 5}" style="width:500px;" autocomplete="off"  placeholder="请输入内容" v-model="form.seo.keywords"></el-input>
                    </el-form-item>
                    <el-form-item label="百度SEO关键词" label-width="120px">
                        <el-input type="textarea" :autosize="{minRows: 5, maxRows: 10}" style="width:500px;" autocomplete="off"  placeholder="请输入内容" v-model="form.seo.description"></el-input>
                    </el-form-item>

                    <div class="wyu-title">
                        <h2>关于我们</h2>
                    </div>
                    <el-form-item label="公司名称" >
                        <el-input v-model="form.company_name" style="width: 500px" placeholder="请输入公司名称"></el-input>
                    </el-form-item>
                    <el-form-item label="公司位置">
                        <amap-dr v-model="addressInfo" :address="addressInfo.address" :lat="addressInfo.lat" :lng="addressInfo.lng"></amap-dr>
                    </el-form-item>

                    <el-form-item label="LOGO" >
                        <wyu-upload :img="form.banner" v-model="form.banner"></wyu-upload>
                        <p class="tit"> 建议尺寸480px*480px或等比缩放</p>
                    </el-form-item>

                    <el-form-item label="公司电话" >
                        <el-input v-model="form.tel" style="width: 500px" placeholder="请输入公司名称"></el-input>
                    </el-form-item>

                    <el-form-item label="联系手机" >
                        <el-input v-model="form.mobile" style="width: 500px" placeholder="请输入联系手机"></el-input>
                    </el-form-item>

                    <el-form-item label="传真" >
                        <el-input v-model="form.fax" style="width: 500px" placeholder="请输入传真"></el-input>
                    </el-form-item>

                    <el-form-item label="邮箱" >
                        <el-input v-model="form.email" style="width: 500px" placeholder="请输入邮箱"></el-input>
                    </el-form-item>

                    <el-form-item label="二维码" >
                        <wyu-upload v-model="form.qrcode" :img="form.qrcode"></wyu-upload>
                        <p class="tit"> 建议尺寸160px*160px或等比缩放</p>
                    </el-form-item>

                    <el-form-item label="公司介绍" >
                        <html-editor title="公司介绍" :html="form.introduce"  v-model="form.introduce"></html-editor>
                    </el-form-item>

                </el-form>
            </div>
        </div>
        <div class="wyu-btn-div">
            <el-button size="small" type="primary"  @click="setAbout()">立即保存</el-button>
            <el-button size="small">取消</el-button>
        </div>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import HtmlEditor from "@/components/HtmlEditor";
    import WyuUpload from "@/components/Photos";
    import * as api from "@/api";
    import AmapDr from "@/components/AmapDr";
    export default {
        name: 'adminRole',
        components: {AmapDr, TopNav,HtmlEditor,WyuUpload},
        data() {
            return {
                loading: false,
                addressInfo: {
                    lat: '',
                    lng: '',
                    address: "",
                },
                form: {
                    company_name: '',
                    introduce: [],
                    banner: '',
                    address: '',
                    lat: '',
                    lng: '',
                    tel: '',
                    mobile: '',
                    fax: '',
                    email: '',
                    qrcode: '',
                    vip_price: 0,
                    vip_huaxian_price: 0,
                    vip_expire_time: 0,
                    is_open: 0,
                    usinessid: '',
                    appkey: '',
                    sold_out: 0,
                    critical: 0,
                    danger: 0,
                    safe: 0,
                    is_fenxiao: 0,
                    user_withdraw_limit: 0,
                    agent_withdraw_limit: 0,
                    seo: {
                        title: '',
                        keywords: '',
                        description: '',
                    }
                }
            }
        },

        created() {
            this.getAbout();
        },

        methods: {
            getAbout(){
                this.loading = true;
                api.post("/shop/admin.setting/getSetting", {
                    appToken: this.$store.getters.appToken,
                }).then(res => {
                    this.form = res.detail;
                    this.addressInfo.address = res.detail.address;
                    this.addressInfo.lat = res.detail.lat -0;
                    this.addressInfo.lng = res.detail.lng - 0;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },

            setAbout() {
                this.loading = true;
                api.post("/shop/admin.setting/setSetting", {
                    appToken: this.$store.getters.appToken,
                    company_name: this.form.company_name,
                    introduce: JSON.stringify(this.form.introduce),
                    banner: this.form.banner,
                    address: this.addressInfo.address,
                    lat: this.addressInfo.lat,
                    lng: this.addressInfo.lng,
                    tel: this.form.tel,
                    mobile: this.form.mobile,
                    fax: this.form.fax,
                    email: this.form.email,
                    qrcode: this.form.qrcode,
                    vip_price: this.form.vip_price,
                    vip_huaxian_price: this.form.vip_huaxian_price,
                    vip_expire_time: this.form.vip_expire_time,
                    is_open: this.form.is_open,
                    usinessid: this.form.usinessid,
                    appkey: this.form.appkey,
                    sold_out: this.form.sold_out,
                    critical: this.form.critical,
                    danger: this.form.danger,
                    safe: this.form.safe,
                    is_fenxiao: this.form.is_fenxiao,
                    user_withdraw_limit: this.form.user_withdraw_limit,
                    agent_withdraw_limit: this.form.agent_withdraw_limit,
                    seo: JSON.stringify(this.form.seo),
                }).then(res => {
                    this.$message({
                        message: '恭喜你，操作成功',
                        type: 'success',
                    });
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            }
        }
    }
</script>
